<template>
  <div style="width: 100%; height: 100%; position: relative;">
    <img v-if="detailLists.length == 0" width="70" height="52"
      style="position: absolute; left: 153px; top: 131px;"
      src="//qianka.b0.upaiyun.com/images/e03cb7b097f15f529f0235549bcd543c.png">

    <div v-if="detailLists.length == 0" class="tips-no-prentice">
      <span>暂无记录</span>
    </div>

    <scroll v-if="detailLists.length > 0" class="container" :on-infinite="infinite">
      <!--可使用-->
      <list class="list-ios chip-detail-list" v-if="type == 'canUse'">
        <item class="chip-detail hl-item-borderless" v-for="chip in detailLists" :key="chip.list.chip_id">
            <div class="title" v-text="chip.date"></div>
            <div class="detail"  v-for="chipL in chip.list" :key="chipL.chip_id"
            :class="{
              'shoutu': chipL.obtain_type == 2 || chipL.obtain_type == 3,
              'gaoe': chipL.obtain_type == 1,
              'credit': chipL.obtain_type == 9,
              'tasks': chipL.obtain_type == 8,
              'tixian': chipL.obtain_type == 4 || chipL.obtain_type == 5 || chipL.obtain_type == 6}"
              >
                <div class="chip-title" v-text="chipText[chipL.obtain_type]"></div>
                <div class="chip-expired_at" v-text="'有效期至 ' + chipL.expired_at"></div>
                <div class="chip-price"><span class="font-big-28">1</span>筹码</div>
                <!-- <div class="chip-title" v-text="chipL.obtain_type == 1 ? '完成高额任务' : (chipL.obtain_type == 2 ? '完成当日收徒1人' : (chipL.obtain_type == 3 ? '完成当日收徒3人' : (chipL.obtain_type == 4 ? '提现30元' : (chipL.obtain_type == 5 ? '提现50元' : '提现100元'))))"></div> -->
            </div>
        </item>
      </list>

      <!--已使用-->
      <list class="list-ios chip-detail-list" v-if="type == 'alreadyUse'">
        <item class="chip-detail hl-item-borderless" v-for="chip in detailLists" :key="chip.list.lottery_id">
            <div class="title" v-text="chip.date"></div>
            <div class="detail already-use-detail"  v-for="chipL in chip.list" :key="chipL.lottery_id">
                <div class="expired-desc" v-text="chipL.lottery_id + '期开奖'"></div>
                <div class="expired-number"><span class="font-big-18">-</span><span class="font-big-28" v-text="chipL.chip_num"></span>筹码</div>
            </div>
         </item>
      </list>

      <!--已过期-->
      <list class="list-ios chip-detail-list" v-if="type == 'overdue'">
        <item class="chip-detail hl-item-borderless" v-for="(chip, index) in detailLists" :key="index">
            <div class="title" v-text="chip.date"></div>
            <div class="detail already-use-detail">
                <div class="expired-desc">筹码过期</div>
                <div class="expired-number"><span class="font-big-18">-</span><span class="font-big-28" v-text="chip.chip_num"></span>筹码</div>
            </div>
         </item>
      </list>

      <div v-if="noMoreData" slot="infinite" class="text-center"></div>
    </scroll>
  </div>
</template>
<style lang="scss" scoped>
  .tips-no-prentice {
    position: absolute;
    left: 0;
    top: 198px;
    width: 100%;
    text-align: center;
    color: #C5C4C3;
    font-size: 16px;
    line-height: 22px;
  }

  .container {
    top: 0px;
    font-family: PingFangSC-Light;

    .chip-detail-list {
      padding-left: 0px;
      padding-top: 0px !important;
      margin-bottom: 0px;
      background-color: #f5f5f5;

      .chip-detail {
        background-color: #f5f5f5;
        padding: 0 26px;
        margin: 23px 0px;

        .title {
          width: 100%;
          font-size: 13px;
          color: #B5B5B5;
          line-height: 18px;
        }

        .detail {
          font-family: PingFangSC-Light;
          background-size: 323px 95px;
          background-repeat: no-repeat;
          padding: 25px 21px !important;
          margin-top: 8px;
          position: relative;

          .chip-title {
            font-size: 14px;
            line-height: 20px;
            color: #484746;
            margin-bottom: 8px;
          }

          .chip-expired_at {
            font-size: 12px;
            color: #888888;
            line-height: 17px;
          }

          .chip-price {
            font-size: 12px;
            color: #873C96;
            position: absolute;
            right: 21px;
            bottom: 24px;

            .font-big-28 {
              font-size: 28px;
              position: relative;
              display: inline-block;
              right: 2px;
            }
          }
        }

        .title + .detail {
          margin-top: 6px;
        }

        .detail.shoutu {
          background-image: url('//qianka.b0.upaiyun.com/images/52c5218de0e151b7797eabb012343294.png');
        }

        .detail.gaoe {
          background-image: url('//qianka.b0.upaiyun.com/images/c3bb6ca9d3d7a93c49e5476c34496319.png');
        }

        .detail.tixian {
          background-image: url('//qianka.b0.upaiyun.com/images/9b1eb5ca9500684ad34a4cf4ef312552.png');
        }

        .detail.credit {
          background-image: url('//qianka.b0.upaiyun.com/images/814875d527f350942ea1f174d7351f78.png');
        }

        .detail.tasks {
          background-image: url('//qianka.b0.upaiyun.com/images/bd8817e62c0c9c69a51b4decb0c9d667.png');
        }

        .detail.already-use-detail {
          background-image: url('//qianka.b0.upaiyun.com/images/195c552478797e210c385e73a8e46ab3.png');
          background-size: 321px 70px;
          padding: 24px 20px !important;

          .expired-desc {
            font-size: 14px;
            color: #484746;
            line-height: 20px;
          }

          .expired-number {
            position: absolute;
            right: 20px;
            bottom: 25px;
            font-size: 12px;
            color: #C5C4C3;

            .font-big-18 {
              font-size: 18px;
              position: relative;
              display: inline-block;
              top: -3px;
              right: 2px;
            }

            .font-big-28 {
              font-size: 28px;
              position: relative;
              display: inline-block;
              top: 3px;
              right: 2px;
            }
          }
        }
      }

      .chip-detail:first-child {
        margin-top: 24px;
      }

      .chip-detail:last-child {
        margin-bottom: 0px;
      }
    }
  }
</style>
<script>
  /* eslint eqeqeq: "off", camelcase: "off" */
  export default {
    props: {
      detailLists: Array,
      type: String,
      infinite: Function,
      noMoreData: Boolean
    },

    data: function () {
      return {
        chipText: {
          1: '完成注册任务',
          2: '完成当日收徒1人',
          3: '完成当日收徒3人',
          4: '提现30元',
          5: '提现50元',
          6: '提现100元',
          8: '完成4个试玩任务',
          9: '完成办卡任务'
        }
      }
    },

    mounted () {
    },

    methods: {}
  }
</script>



