<!-- 筹码明细 -->
<template>
  <div class="page" v-nav="{title: '明细', showBackButton: true, onBackButtonClick: back}">
    <div class="page-content">
      <div class="bar bar-subheader"  @touchmove="$event => $event.preventDefault()">
        <div class="button-bar" :style="{minWidth: barWidth + 'px'}">
          <router-link v-for="(link, index) in tabLinks" :key="index" :to="link.to"
            class="button font-regular"  @click.native="onTabClick(index)"
            v-text="link.text">
          </router-link>
        </div>
        <div class="hairline-bottom"></div>
      </div>

      <!-- 可使用 -->
      <keep-alive>
        <chip-list v-if="loaded && activeIndex == 0" :type='type'
        :detailLists='canUseLists' :infinite="infinite" :noMoreData="noMoreAvailableData"></chip-list>
      </keep-alive>

      <!-- 已使用 -->
      <keep-alive>
        <chip-list v-if="loaded && activeIndex == 1" :type='type'
        :detailLists='alreadyUseList' :infinite="infinite" :noMoreData="noMoreUsedData"></chip-list>
      </keep-alive>

      <!-- 已过期 -->
      <keep-alive>
        <chip-list v-if="loaded && activeIndex == 2" :type='type'
        :detailLists='overdueList' :infinite="infinite" :noMoreData="noMoreExpiredData"></chip-list>
      </keep-alive>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import '../../../scss/variables';

  .page-content {
    padding-top: 44px + 50px;
  }

  .button-bar > .button:first-child {
    border-radius: 2px 0 0 2px;
  }

  .button-bar > .button:last-child {
    border-radius: 0 2px 2px 0;
  }

  .bar.bar-subheader {
    height: 50px;

    .button-bar {
      padding: 5px 0;
      margin: 0 auto;
      min-width: 270px;
      flex: 0;
      -webkit-box-flex: 0;

      .button {
        height: 30px;
        min-height: 30px;
        line-height: 28px;
        color: #484746;

        &.router-link-active {
          background: #873C96;
          color: white;
        }
      }
    }
  }
</style>
<script>
  import chipList from './List'
  const PAGE_SIZE = 10

  export default {
    components: {
      chipList
    },
    data: function () {
      return {
        tabLinks: [
          {
            to: { path: '/lottery/chipsDetail/canUse' },
            text: '可使用'
          },
          {
            to: { path: '/lottery/chipsDetail/alreadyUse' },
            text: '已使用'
          },
          {
            to: { path: '/lottery/chipsDetail/overdue' },
            text: '已过期'
          }
        ],
        barWidth: 321,
        activeIndex: 2,
        loaded: false,
        canUseLists: [],
        alreadyUseList: [],
        overdueList: [],
        type: '',
        availableOffset: undefined,
        usedOffset: undefined,
        expiredOffset: undefined,
        noMoreAvailableData: true,
        noMoreUsedData: true,
        noMoreExpiredData: true
      }
    },

    mounted () {
      this.fetchAllData()

      if (this.$route.params.type === 'canUse') {
        this.activeIndex = 0
        this.type = 'canUse'
      }
      if (this.$route.params.type === 'alreadyUse') {
        this.activeIndex = 1
        this.type = 'alreadyUse'
      }
      if (this.$route.params.type === 'overdue') {
        this.activeIndex = 2
        this.type = 'overdue'
      }
    },

    methods: {
      back () {
        $router.back('/lottery/getChips')
      },

      fetchAllData () {
        $loading.show('加载中')
        $api
          .lotteryChipAll()
          .then(res => res.data && res.data.payload)
          .then(payload => {
            this.canUseLists = payload.available
            this.alreadyUseList = payload.used
            this.overdueList = payload.expired
            this.noMoreAvailableData = payload.available.length === 0 || payload.available.length < PAGE_SIZE
            this.noMoreUsedData = payload.used.length === 0 || payload.used.length < PAGE_SIZE
            this.noMoreExpiredData = payload.expired.length === 0 || payload.expired.length < PAGE_SIZE

            _.each(payload.available, (a, i) => {
              if (i === payload.available.length - 1) {
                this.availableOffset = a.date
              }
            })

            _.each(payload.used, (u, i) => {
              if (i === payload.used.length - 1) {
                this.usedOffset = u.date
              }
            })

            _.each(payload.expired, (e, i) => {
              if (i === payload.expired.length - 1) {
                this.expiredOffset = e.date
              }
            })

            this.loaded = true
            $loading.hide()
          })
      },

      infinite (done) {
        if (this.type === 'canUse') {
          if (this.availableOffset === undefined || this.noMoreAvailableData) return
          $api
            .lotteryChipAvailable(this.availableOffset)
            .then(res => res.data && res.data.payload)
            .then(payload => {
              _.each(payload, (a, i) => {
                if (i === payload.length - 1) {
                  this.availableOffset = a.date
                }
              })

              this.canUseLists = this.canUseLists.concat(payload)
              this.noMoreAvailableData = payload.length === 0 || payload.length < PAGE_SIZE
              done()
            })
        }

        if (this.type === 'alreadyUse') {
          if (this.usedOffset === undefined || this.noMoreUsedData) return
          $api
            .lotteryChipUsed(this.usedOffset)
            .then(res => res.data && res.data.payload)
            .then(payload => {
              _.each(payload, (u, i) => {
                if (i === payload.length - 1) {
                  this.usedOffset = u.date
                }
              })

              this.alreadyUseList = this.alreadyUseList.concat(payload)
              this.noMoreUsedData = payload.length === 0 || payload.length < PAGE_SIZE
              console.log('alreadyUse infinite over')
              done()
            })
        }

        if (this.type === 'overdue') {
          if (this.expiredOffset === undefined || this.noMoreExpiredData) return
          $api
            .lotteryChipExpired(this.expiredOffset)
            .then(res => res.data && res.data.payload)
            .then(payload => {
              _.each(payload, (e, i) => {
                if (i === payload.length - 1) {
                  this.expiredOffset = e.date
                }
              })

              this.overdueList = this.overdueList.concat(payload)
              this.noMoreExpiredData = payload.length === 0 || payload.length < PAGE_SIZE
              done()
            })
        }
      },

      onTabClick (index) {
        this.activeIndex = index
        index === 0 ? this.type = 'canUse' : (index === 1 ? this.type = 'alreadyUse' : this.type = 'overdue')
      }
    }
  }
</script>



